		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.withParents-vocabulary-lists{
				&__area{}
				&__box{padding: 107px 57px 0;
					.tab{
						&__items{height: 102px;display: flex;justify-content: center;box-sizing: border-box}
						&__item{height: 79px;background: no-repeat 0 0;
							&:nth-last-of-type(1){margin-right: 0 !important;}
							&.default{width: 216px;background-image: url("/assets/images/with_parents_vocabulary_lists/bg_tab01_default.png");margin-right: 30px;}
							&.active{width: 224px;background-image: url("/assets/images/with_parents_vocabulary_lists/bg_tab01_active.png");margin-right: 22px;
								.btn{color: #5d9eed;}
							}
							.btn{width: 216px;height: 60px;
								&-tab{@include font($gro, $font-r, 28px, 64px, #fff);letter-spacing: 2px;}
							}
						}
					}
					.select-box{position: relative;margin: 0 auto;height: 52px;
						&__val{position: relative;height: 52px;text-align: left;background: url("/assets/images/with_parents_vocabulary_lists/bg_select_box01.png") no-repeat 0 0;z-index: 1;
							.btn{
								&-select-open{width: 100%;text-align: left;background: url("/assets/images/with_parents_vocabulary_lists/icon_select_arrow01.png") no-repeat calc(100% - 12px) 18px;
									.val{display: inline-block;width: 153px;@include font($notoecp, $font-b, 25px, 52px, #fff);letter-spacing: 1px;vertical-align: top;text-align: center;}
								}
							}
						}
						&__layer{display: none;position: absolute;top: 42px;left: 0;width: 100%;height: 230px;padding: 25px 0;background: url("/assets/images/with_parents_vocabulary_lists/bg_select_box01_lists.png") no-repeat 0 100%;box-sizing: border-box;
							.btn{
								&-select{width: 100%;height: 100%;
									span{@include font($notoecp, $font-b, 27px, 40px, #fff);letter-spacing: 1px;transition: all 0.3s}
									&.default{span{}}
									&.this-week{span{font-size: 33px;}}
									&.disabled{span{opacity: 0.5;font-weight: $font-r;}}
								}
							}
						}
						&__wrap{position: absolute;top: 0;left: 0;width: 203px;height: 52px;}
						.swiper{
							&-container{height: 186px;width: 193px;padding-right: 3px;text-align: left;margin: 0;}
							&-wrapper{}
							&-controller{}
							&-pagination{}
							&-slide{height: auto !important;margin: -5px 0;
								li{text-align: center;}
							}
							&-scrollbar{right: 0;background: none;
								&-drag{width: 3px;background-color: rgba(255,255,255,0.5);}
							}
						}
					}
					.lists{
						&__items{display: flex;align-content:space-between; flex-flow: row wrap;}
						&__item{width: 157px;height: 219px;margin: 16px 12px 0;text-align: center;background: url("/assets/images/with_parents_vocabulary_lists/bg_vocabulary_list01.png") no-repeat 0 0;}
						.btn{
							&-item{}
						}
						.img{display: flex; width: 133px;height: 133px;margin: 13px auto 0;justify-content: center;align-items: center;
							img{max-width: 100%;max-height: 100%;}
						}
						.txt{
							&-box{}
							&-info{padding: 20px 10px 0;
								&.txt{
									&-high{@include font($notoecp, $font-r, 14px, 19px, #216ac3);}
									&-low{@include font($notoecp, $font-r, 19px, 19px, #216ac3);}
								}
							}
						}
					}
					.print{position: absolute;top: 203px;right: 23px;
						.btn{
							&-print{}
						}
						.icon{display: inline-block;
							&-print{width: 73px;height: 73px;background: url("/assets/images/with_parents_vocabulary_lists/icon_print01.png") no-repeat 0 0;}
						}
					}
					.tit{@include font($nsroundecp, $font-b, 24px, 24px, #5d9eed);position: absolute;top: 0;left: 0;}
					.data-table{overflow: hidden;border-radius: 8px 8px 0 0;
						table{width: 100%;text-align: center;}
						tr{
							&.default{
								.btn{
									&-link{color: #7c7c7c;}
								}
							}
							&.active{
								&:not(.this-week):not(.vacation){
									.td{
										&-subject{background-color: #f0feff !important;}
										&-audio{background-color: #f0feff;}
										&-download{background-color: #f0feff;}
									}
								}

							}
							&.this-week{background-color: #f3f9f9;
								.td{
									&-week{background-color: #5d9eed;
										&:after{background-color: #f5f5f1;}
										.week{color: #fff;}
										.num{color: #fff;}
									}
									&-subject{background-color: #f3f9f9;
										.btn{
											&-link{color: #629bf1;}
										}
										&:after{background-color: #f3f9f9;}
									}
								}
							}
							&.disabled{
								.td{
									&-week{
										.week{color: #cbcbca;}
										.num{color: #cbcbca;}
									}
								}
							}
							&.vacation{
								.td{
									&-subject{background-color: #f5f5f1;}
									&-audio{background-color: #f5f5f1;}
									&-download{background-color: #f5f5f1;}
								}
							}
						}
						th{position: relative;height: 50px;@include font($notoecp, $font-b, 20px, 54px, #5d9eed);background-color: rgba(152,198,255, 0.5);
							&:after{content:"";position: absolute;bottom: 0;left: 0;width: 100%;height: 2px;background: #fffff7 url("/assets/images/with_parents_reports/bl_line_row01.png") repeat-x 0 100%;}
						}
						td{position: relative;height: 63px;@include font($notoecp, $font-b, 22px, 28px, #7c7c7c);vertical-align: middle;background: url("/assets/images/with_parents_reports/bl_line_col01.png") repeat-y 0 0;
							&:after{content:"";position: absolute;bottom: 0;left: 0;width: 100%;height: 2px;background: url("/assets/images/with_parents_reports/bl_line_row01.png") repeat-x 0 100%;}
						}
						.td{
							&-week{background: #f5f5f1 none;
								.week{display: block;@include font($nsroundecp, $font-b, 13px, 13px, #919191);text-transform: uppercase;}
								.num{display: block;margin-top: 4px;@include font($nsroundecp, $font-b, 25px, 21px, #919191);}
							}
							&-subject{padding-left: 13px;@include font($notoecp, $font-b, 19px, 19px, #919191);text-align: left;background: none;}
							&-audio{padding: 0 13px;
								.audio{
									&-controller{float: left;margin-right: 5px;}
									&-lists{float: left;}
									&-list{float: left;}
								}
								.btn{vertical-align: top;
									&-audio{width: 37px;height: 37px;}
									&-num{
										span{display: inline-block;width: 40px;height: 37px;@include font($nsroundecp, $font-b, 22px, 41px, #cbcbca);vertical-align: top;}
										&.active{
											span{color: #5c9ded;}
										}
									}
								}
								.icon{display: inline-block;vertical-align: top;width: 37px;height: 37px;transition: all 0.3s;
									&-play{background: url("/assets/images/with_parents_meet_greet/icon_play01.png") no-repeat 0 0;}
									&-pause{background: url("/assets/images/with_parents_meet_greet/icon_pause01.png") no-repeat 0 0;}
								}
							}
							&-download{
								.btn{vertical-align: top;
									&-download{
										span{display: inline-block;min-width: 53px;height: 37px;padding: 0 8px;@include font($nsroundecp, $font-b, 17px, 36px, #919191);border: 2px solid #fc6492;color: #fc6492;background-color: #ffffff;border-radius: 37px;vertical-align: top;box-sizing: border-box;text-transform: uppercase;}
										&.active{
											span{padding: 0 6px;@include font($nsroundecp, $font-b, 17px, 31px, #919191);border: 4px solid #fd9ab2;color: #fff;background-color: #fc6492;border-radius: 37px;vertical-align: top;box-sizing: border-box;text-transform: uppercase;}
										}
									}
								}
							}
						}
					}
				}
			}
			.swiper{position: relative;
				&-container{}
				&-wrapper{}
				&-slide{}
				&-pagination {bottom: -41px;left: 0;width: 100%;align-content: center;justify-content: center;flex-wrap: wrap;
					&-bullet{flex:1; width: 32px;height: 32px;opacity: 1;transition: all 0.3s;margin: 0 15px;@include font($nsroundecp, $font-b, 19px, 36px, #fff);background: url("/assets/images/with_parents_meet_greet/icon_paging_num.png") no-repeat 0 0;
						&-active{color: #fff;background-image: url("/assets/images/with_parents_meet_greet/icon_paging_num_active.png");}
					}
				}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 50%;left: -42px;width: 30px;height: 58px;margin-top: -29px;background: url("/assets/images/with_parents_meet_greet/icon_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 50%;right: -42px;width: 30px;height: 58px;margin-top: -29px;background: url("/assets/images/with_parents_meet_greet/icon_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
			}
		}
	//}
