.vocabulary-card{position: relative;width: 1024px;height: 768px;margin: 0 auto;z-index: $z-layer2;
	&__content{
		.swiper{
			&-container{width: 1024px;height: 637px;overflow: hidden;
				&.active{overflow: visible;}
			}
			&-wrapper{position: relative;height: 100%;}
			&-slide{position: relative;
			}
			&-slide-active{
				&.motion{transform: scale(1);transition: all 0.3s}
			}
			&-slide-prev{transform: scale(0.4);
				&.motion{transition: all 0.3s}
			}
			&-slide-next{transform: scale(0.4);
				&.motion{transition: all 0.3s}
			}
			&-controller{
				.btn-swiper-prev{position: absolute;top: 280px;left: 148px;width: 40px;height: 79px;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 1;
					&.swiper-button-disabled{opacity: 0;}
				}
				.btn-swiper-next{position: absolute;top: 280px;right: 148px;width: 40px;height: 79px;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 1;
					&.swiper-button-disabled{opacity: 0;}
				}
			}
		}
	}
	&__items{position: relative;width: 553px;height: 558px;padding: 44px 0 44px 0;margin: 0 auto;}
	&__item{position: relative;width: 553px;height: 558px;display: flex;flex-direction: column; align-items: center;justify-content: center;flex-wrap: wrap;margin: 0 auto;background: url("/assets/images/with_parents_vocabulary_lists/popup/bg_card01.png") no-repeat 0 0;
		&.all{
			.txt{margin-top: 47px;}
		}
		&.text{
			.img{display: none;}
		}
		&.picture{
			.txt{display: none;}
		}
		.img{
			img{max-width: 372px;min-height: 364px;}
		}
		.txt{
			&.txt{
				&-high{@include font($notoecp, $font-r, 34px, 45px, #11c5b4);}
				&-low{@include font($notoecp, $font-r, 45px, 45px, #11c5b4);}
			}
		}
	}
	&__controller{position: absolute;bottom: 72px;left: 0;width: 100%;
		&__items{display: flex;justify-content: center;align-items: center;}
		&__item{flex-direction: column;}
		.btn{
			&-type{padding: 0 34px;height: 60px;border: 5px solid #4acfbd;box-sizing: border-box;margin: 0 9px;min-width: 168px;
				&.default{@include font($notoecp, $font-r, 30px, 50px, #fff);border-radius: 30px;background-color: #4acfbd;}
				&.active{@include font($notoecp, $font-r, 30px, 50px, #4acfbd);border-radius: 30px;background-color: #fff;}
			}
		}
	}
	.btn{
		&-card{width: 100%;height: 100%;}
	}
	.btn{overflow: visible;
		&-close{position: absolute;top: 64px;right: 20px;width: 36px;height: 36px;transition: all 0.3s;border-radius: 4px;

		}
	}

	.icon{display: block;background-color: transparent;
		&-oscar{position: absolute;top: -128px;left: -157px;width: 474px;height: 475px;animation: oscar-icon 1s steps(9) 1 forwards;background: url("/assets/images/sprite/flashcard_oscar1.png") no-repeat 0 0;}
		&-close{width: 100%;height: 100%;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_close01.png") no-repeat 50% 50%;}
	}
}




/* 프린트 팝업 영역 */
.controller{
	&__area{}
	&__box{}
	&__items{}
	&__item{display: flex;margin-top: 10px;
		&:nth-of-type(1){margin-top: 0;}
	}
	.radio{
		&:nth-of-type(1){width: 220px;}
		&:nth-of-type(2){width: 266px;}
		input[type="radio"]{position: absolute;top: 0;left: 0;opacity: 0;
			&:checked + .radio__txt .icon-check:before{opacity: 1;}
		}
		&__txt{position: relative;display: block;padding-left: 56px;height: 43px;@include font($nsroundecp, $font-b, 23px, 47px, #11c5b4);}
	}
	.btn{
		&-print{position: absolute;top: 20px;right: 20px;transition: all 0.3s;}
		&-pdf{position: absolute;bottom: 0;right: 20px;}
	}
	.icon{display: inline-block;vertical-align: top;
		&-print{width: 187px;height: 71px;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_print01.png") no-repeat 0 0;
			&.default{}
			&.active{}
		}
		&-pdf{width: 70px;height: 70px;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_1x1.png") no-repeat 0 0;}
		&-check{position: absolute;top: 0;left: 0;width: 43px;height: 43px;border-radius: 14px;border: 2px solid #dfdfdf;box-sizing: border-box;
			&:before{content:"";position: absolute;top: 7px;left: 4px;width: 30px;height: 24px;background: url("/assets/images/with_parents_vocabulary_lists/popup/icon_check01.png") no-repeat 50% 50%;opacity: 0;transition: all 0.3s}
		}
	}
}

