		&.all{
		}
		&.text{
			.img-box{display: none;}
		}
		&.picture{
			.txt-box{display: none;}
		}
		.img{
		}
	}
	.tit{position: absolute;top: 12px;left: -110px;@include font($nsroundecp, $font-b, 22px, 1em, #11c5b4);letter-spacing: 1px;}
}

@media print{
	.controller{display: none;}

	.preview{
		&__area{}
		&__box{position: relative;margin:0;border: none;}
		&__items{display: flex;flex-direction: row;align-items: center;width: 297mm;height: 209mm;border: none;margin: 0;box-sizing: border-box;page-break-before: always;
			&.size1by1{
				.txt{
